<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;text-align: left">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder="ID"></el-input>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>	
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>		
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>	
				</el-form-item>
				<el-form-item>
					<el-button-group>
						<el-button type="primary" @click="getUsers" icon="el-icon-search"></el-button>
						<el-button type="primary" @click="" icon="el-icon-search"></el-button>
						<el-button type="primary" @click="handleAdd" icon="el-icon-plus"></el-button>	
					</el-button-group>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table 
			:data="users" 
			stripe
			highlight-current-row 
			v-loading="listLoading" 
			@selection-change="selsChange" 
			style="width: 100%;text-align:left"
		>
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column type="index" width="60">
			</el-table-column>
			<el-table-column prop="id" label="ID" width="150" sortable>
			</el-table-column>
			<el-table-column prop="name" label="" width="150"  sortable>
			</el-table-column>
			<el-table-column prop="role" label="" width="200" sortable>
			</el-table-column>
			<el-table-column prop="team" label=""  sortable>
			</el-table-column>
			<el-table-column label="" width="200">
				<template slot-scope="scope">
					<el-button-group>						
						<el-button type="primary" size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
						<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
					</el-button-group>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar" style="text-align:left">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog 
				title="" 
				:visible.sync="editFormVisible" 
		>
			<el-form 
					:model="editForm" 
					label-width="80px" 
					:rules="editFormRules" 
					ref="editForm"
			>
				<el-form-item label="ID" prop="id">
					<el-input v-model="editForm.id" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="editForm.role" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="editForm.team" auto-complete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>

		<!---->
		<el-dialog 
				title="" 
				:visible.sync="addFormVisible" 
		>
			<el-form 
					:model="addForm" 
					label-width="100px" 
					:rules="addFormRules" 
					ref="addForm"
					size="medium"
					label-position="right"
			>
				<el-form-item label="ID" prop="id">
					<el-input v-model="addForm.id" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label=""  prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label=""  prop="role" style="text-align:left">
					<el-select 
						v-model="addForm.role" 
						placeholder=""
						clearable
						filterable
					>
						<el-option
							v-for="item in Roles"
							:key="item.id"
							:label="item.value"
							:value="item.value"
						></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label=""  prop="team" style="text-align:left">
					<!-- <el-input v-model="editForm.team" auto-complete="off"></el-input> -->
					<el-select 
						v-model="addForm.team" 
						placeholder=""
						clearable
						filterable
					>
						<el-option
							v-for="item in Teams"
							:key="item.id"
							:label="item.value"
							:value="item.value"
						></el-option>
					</el-select>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	// import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getUserListPage, removeUser, batchRemoveUser, editUser, addUser } from '@/api/api';

	export default {
		data() {
			return {
				filters: {
					name: ''
				},
				users: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//

				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				editForm: {
					id: '',
					name: '',
					role: '',
					team: ''
				},

				addFormVisible: false,//
				addLoading: false,
				addFormRules: {   //
					id: [
						{ required: true, message: 'ID', trigger: 'blur' }
					],
					name: [
						{ required: true, message: '', trigger: 'blur' }
					],
					role: [
						{ required: true, message: '', trigger: 'blur' }
					],
					team: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				addForm: {
					id: '',
					name: '',
					role: '',
					team: ''
				},
				//
				Roles: [
							{
								id: 1,
							 	value:''
							},
							{
								id: 2,
								value:''
							},
							{
								id: 3,
								value: ''
							},
							{
								id: 4,
								value: ''
							}
						],
				//
				Teams: [
							{
								id: 1,
								value:''
							}, 
							{
								id: 2,
								value:''
							}, 
							{
								id: 3,
								value:''
							}, 
							{
								id: 4,
								value:''
							}, 
							{
								id: 5,
								value:''
							}, 
							{
								id: 6,
								value:''
							}, 
							{
								id: 7,
								value:''
							}
						]

			}
		},
		methods: {
			//
			formatSex: function (row, column) {
				return row.sex == 1 ? '' : row.sex == 0 ? '' : '';
			},
			handleCurrentChange(val) {
				this.page = val;
				this.getUsers();
			},
			//
			getUsers() {
				let para = {
					page: this.page,
					name: this.filters.name
				};
				this.listLoading = true;
				//NProgress.start();
				getUserListPage(para).then((res) => {
					this.total = res.data.total;
					this.users = res.data.users;
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//
			handleDel: function (index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { id: row.id };
					removeUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			},
			//
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					id: '',
					name: '',
					role: '',
					team: ''
				};
			},
			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							editUser(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			//
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							addUser(para).then((res) => {
								this.addLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//
			batchRemove: function () {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { ids: ids };
					batchRemoveUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			}
		},
		mounted() {
			this.getUsers();
		}
	}

</script>

<style scoped>

</style>


