<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;text-align: left">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>	
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>	
				</el-form-item>
				<el-form-item>
					<el-button-group>
						<el-button type="primary" @click="getCusts" icon="el-icon-search"></el-button>
						<el-button type="primary" @click="" icon="el-icon-search"></el-button>
						<el-button type="primary" @click="handleAdd" icon="el-icon-plus"></el-button>
						<el-button type="primary" @click="handleUpload" icon="el-icon-upload"></el-button>	
					</el-button-group>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table 
			:data="custs" 
			stripe
			highlight-current-row 
			v-loading="listLoading" 
			@selection-change="selsChange" 
			style="width: 100%;text-align:left"
			height="530"
		>
			<el-table-column fixed type="selection" width="55">
			</el-table-column>
			<el-table-column type="index" width="60">
			</el-table-column>
			<el-table-column prop="bankid" label="" width="150" sortable>
			</el-table-column>
			<el-table-column prop="bankname" label="" width="150"  sortable>
			</el-table-column>
			<el-table-column prop="custid" label="" width="200" sortable>
			</el-table-column>
			<el-table-column prop="custname" label="" width="200" sortable>
			</el-table-column>
			<el-table-column prop="custtype" label="" width="200" sortable>
			</el-table-column>
			<el-table-column prop="custnetincome" label="()" width="200" sortable>
			</el-table-column>
			<el-table-column prop="team" label="" width="200" sortable>
			</el-table-column>
			<el-table-column fixed="right" label="" width="200">
				<template slot-scope="scope">
					<el-button-group>						
						<el-button 
							type="primary" 
							size="small" 
							@click="handleEdit(scope.$index, scope.row)"
							v-show="!scope.row.allocated"
						></el-button>
						<el-button 
							type="success" 
							size="small" 
							@click="handleEdit(scope.$index, scope.row)"
							v-show="scope.row.allocated"
						></el-button>
						<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
					</el-button-group>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar" style="text-align:left">
			<el-button 
				type="danger" 
				@click="batchRemove" 
				:disabled="this.sels.length===0"
				size="small"
			></el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog 
				title="" 
				:visible.sync="editFormVisible" 
		>
			<el-form 
					:model="editForm" 
					label-width="120px" 
					:rules="editFormRules" 
					ref="editForm"
			>
				<el-form-item label="" prop="bankid">
					<el-input v-model="editForm.bankid" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="bankname">
					<el-input v-model="editForm.bankname" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="custid">
					<el-input v-model="editForm.bankid" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="custname">
					<el-input v-model="editForm.custname" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="editForm.custtype" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="()">
					<el-input v-model="editForm.custnetincome" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" style="text-align:left" prop="team">
					<el-select 
						v-model="editForm.team" 
						placeholder=""
						clearable
						filterable
						@change="teamChange"
					>
						<el-option
							v-for="item in Teams"
							:key="item.id"
							:label="item.value"
							:value="item.value"
						></el-option>
					</el-select>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer" style="text-align: center">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>

		<!---->
		<el-dialog 
				title="" 
				:visible.sync="addFormVisible" 
		>
			<el-form 
					:model="addForm" 
					label-width="100px" 
					:rules="addFormRules" 
					ref="addForm"
					size="medium"
					label-position="right"
			>
				<el-form-item label="ID" prop="id">
					<el-input v-model="addForm.id" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label=""  prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label=""  prop="role" style="text-align:left">
					<el-select 
						v-model="addForm.role" 
						placeholder=""
						clearable
						filterable
					>
						<el-option
							v-for="item in Roles"
							:key="item.id"
							:label="item.value"
							:value="item.value"
						></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label=""  prop="team" style="text-align:left">
					<!-- <el-input v-model="editForm.team" auto-complete="off"></el-input> -->
					<el-select 
						v-model="addForm.team" 
						placeholder=""
						clearable
						filterable
					>
						<el-option
							v-for="item in Teams"
							:key="item.id"
							:label="item.value"
							:value="item.value"
						></el-option>
					</el-select>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer" style="text-align:center">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>

		<!--  -->
		<el-dialog 
				title="" 
				:visible.sync="uploadFormVisible" 
		>
			<el-upload
				  class="upload-demo"
				  ref="upload"
				  action="https://jsonplaceholder.typicode.com/posts/"
				  :on-preview="handlePreview"
				  :on-remove="handleRemove"
				  :on-change="handleUploadData"
				  :file-list="fileList"
				  :auto-upload="false">
				  <el-button slot="trigger" size="small" type="primary"></el-button>
			  <div slot="tip" class="el-upload__tip">Excel</div>
			</el-upload>
			
		</el-dialog>

	</section>
</template>

<script>
	// import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import FileSaver from 'file-saver'
    import XLSX from 'xlsx'

	import { getCustListPage, removeUser, batchRemoveUser, editCust, addUser, addCusts } from '@/api/api';

	export default {
		data() {
			return {
				filters: {
					name: ''
				},
				custs: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//

				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
					team: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				editForm: {
					bankid: '',
					bankname: '',
					custid: '',
					custname: '',
					custtype: '',
					custnetincome: 0.0,
					team: ''
				},

				addFormVisible: false,//
				addLoading: false,
				addFormRules: {   //
					id: [
						{ required: true, message: 'ID', trigger: 'blur' }
					],
					name: [
						{ required: true, message: '', trigger: 'blur' }
					],
					role: [
						{ required: true, message: '', trigger: 'blur' }
					],
					team: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				addForm: {
					bankid: '',
					bankname: '',
					custid: '',
					custname: '',
					custtype: '',
					custnetincome: 0.0,
					team: ''
				},
				//
				Roles: [
							{
								id: 1,
							 	value:''
							},
							{
								id: 2,
								value:''
							},
							{
								id: 3,
								value: ''
							},
							{
								id: 4,
								value: ''
							}
						],
				//
				Teams: [
							{
								id: 1,
								value:''
							}, 
							{
								id: 2,
								value:''
							}, 
							{
								id: 3,
								value:''
							}, 
							{
								id: 4,
								value:''
							}, 
							{
								id: 5,
								value:''
							}, 
							{
								id: 6,
								value:''
							}, 
							{
								id: 7,
								value:''
							}
						],
				uploadFormVisible: false,
				fileList: []

			}
		},
		methods: {
			submitUpload() {
				console.log("this.fileList:",this.fileList)
				console.log("this.$refs:",this.$refs)
		        this.$refs.upload.submit();

		    },
		    handleRemove(file, fileList) {
		        console.log("file",file,"fileList", fileList);
		    },
		    handlePreview(file) {
		        console.log("file",file);
		    },
			//
			formatSex: function (row, column) {
				return row.sex == 1 ? '' : row.sex == 0 ? '' : '';
			},
			handleCurrentChange(val) {
				this.page = val;
				this.getCusts();
			},
			//
			getCusts() {
				let para = {
					page: this.page,
					name: this.filters.name
				};
				this.listLoading = true;
				//NProgress.start();
				getCustListPage(para).then((res) => {
					this.total = res.data.total;
					this.custs = res.data.custs;
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//
			handleDel: function (index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { bankid: row.bankid, custid: row.custid };
					removeUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getCusts();
					});
				}).catch(() => {

				});
			},
			//
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					bankid: '',
					bankname: '',
					custid: '',
					custname: '',
					custtype: '',
					custnetincome: 0.0,
					team: ''
				};
			},
			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							// para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							console.log("para:",para);
							editCust(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getCusts();
							});
						});
					}
				});
			},
			//
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							addUser(para).then((res) => {
								this.addLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getCusts();
							});
						});
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//
			batchRemove: function () {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { ids: ids };
					batchRemoveUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getCusts();
					});
				}).catch(() => {

				});
			},
			handleUpload(){
				this.uploadFormVisible = true;       

			},
			handleUploadData(file, fileList){
				console.log("file:",file,"fileList:",fileList)
				// var files = file
				// var fileReader = new FileReader();
	   //          fileReader.onload = function(ev) {
	   //              try {
	   //                  var data = ev.target.result,
	   //                      workbook = XLSX.read(data, {
	   //                          type: 'binary'
	   //                      }), // excel
	   //                      persons = []; // 
	   //              } catch (e) {
	   //                  console.log('');
	   //                  return;
	   //              }

	   //              // 
	   //              var fromTo = '';
	   //              // 
	   //              for (var sheet in workbook.Sheets) {
	   //                  if (workbook.Sheets.hasOwnProperty(sheet)) {
	   //                      fromTo = workbook.Sheets[sheet]['!ref'];
	   //                      console.log(fromTo);
	   //                      persons = persons.concat(XLSX.utils.sheet_to_json(workbook.Sheets[sheet]));
	   //                      // break; // 
	   //                  }
	   //              }

	   //              console.log(persons);
	            // };

	            // // 
	            // fileReader.readAsBinaryString(file.raw);

	            this.uploadButtonLoading = true
	            const fileReader = new FileReader(); 

	            //
	            fileReader.readAsBinaryString(file.raw); 

	            // 
	            fileReader.onload = (ev) => { 
	            	try{ 
	            		const data = ev.target.result; 
	            		console.log("data:",data)
	            		const workbook = XLSX.read(data, { type: 'binary' }); 
	            		console.log("workbook:",workbook)
	            		const sheetArray = XLSX.utils.sheet_to_json(workbook.Sheets['Sheet1']);
	            		console.log("sheetArray:",sheetArray) 
	            		// for (let sheet in workbook.Sheets) { 
	            		// 	const sheetArray = XLSX.utils.sheet_to_json(workbook.Sheets[sheet]); 
	            		// 	console.log("sheet:",sheet)
	            		// 	console.log("sheetArray:",sheetArray)
	            	 //    }

	            	    //
	            	    let para = []
	            	    for(var i=0; i<sheetArray.length; i++){
	            	        let element = 
		            	    {
								bankid: sheetArray[i][''],
								bankname: sheetArray[i][''],
								custid: sheetArray[i][''],
								custname: sheetArray[i][''],
								custtype: sheetArray[i][''],
								custnetincome: sheetArray[i][''],
								team: '',
								allocated: false,
								profitdistributed: false
							}
							para.push(element)
	            	    }
	            	    
	            	    //
	            	    addCusts(para).then((resp) => {
	            	    	console.log("resp:",resp)
	            	    	this.uploadButtonLoading = false
	            	    	this.uploadFormVisible = false
	            	    	this.getCusts()
	            	    	this.$message.success('')
	            	    });



	            	    

	            	}catch(e){ 
	            		this.uploadButtonLoading = false
	            		this.$message.warning(''); 
	            		return false; 
	            	} 
	            
		        }


			},
			teamChange(){
				this.editForm.allocated = true
			}
		},
		mounted() {
			this.getCusts();
		}
	}

</script>

<style scoped>

</style>


