import Mock from 'mockjs';
import admin from '@/assets/admin.png'

const LoginUsers = [
  {
    id: 1,
    username: 'admin',
    password: '123456',
    avatar: admin,
    name: ''
  }
];
//
const Users = [];

for (let i = 0; i < 100; i++) {
  Users.push(Mock.mock({
    id: Mock.Random.string('number',6,6),   //ID
    name: Mock.Random.cname(),
    role: Mock.Random.pick(['','','','']),
    team: Mock.Random.pick([
                              '',
                              '',
                              '',
                              '',
                              '',
                              '',
                              ''])
  }));
}

