const CompCusts = []

for (let i = 0; i < 100; i++) {
  CompCusts.push(Mock.mock({
    bankid: Mock.Random.string('number',6,6),   //ID
    bankname: Mock.Random.pick([                //
                                '',
                                '',
                                '',
                                '',
                                ''
                                ]),
    custid: Mock.Random.string('number',6,6),   //
    custname: Mock.Random.cname()+'',              //
    custtype: Mock.Random.pick(['','','']),  //
    custnetincome: Mock.Random.float(10000,200000,2,2), //
    team: '',       //
    allocated: false,   //
    profitdistributed: false    //
  }));
}

const ProfitShareResult = []


export { LoginUsers, Users ,Roles, Teams, CompCusts, ProfitShareResult};


