<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;text-align: left">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>	
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>	
				</el-form-item>
				<el-form-item>
					<el-button-group>
						<el-button type="primary" @click="getCusts" icon="el-icon-search"></el-button>
						<el-button type="primary" @click="" icon="el-icon-search"></el-button>
					</el-button-group>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table 
			:data="custs" 
			stripe
			highlight-current-row 
			v-loading="listLoading" 
			@selection-change="selsChange" 
			style="width: 100%;text-align:left"
			height="530"
		>
			<el-table-column fixed type="selection" width="55">
			</el-table-column>
			<el-table-column type="index" width="60">
			</el-table-column>
			<el-table-column prop="bankid" label="" width="150" sortable>
			</el-table-column>
			<el-table-column prop="bankname" label="" width="150"  sortable>
			</el-table-column>
			<el-table-column prop="custid" label="" width="200" sortable>
			</el-table-column>
			<el-table-column prop="custname" label="" width="200" sortable>
			</el-table-column>
			<!-- <el-table-column prop="custtype" label="" width="200" sortable>
			</el-table-column> -->
			<el-table-column prop="custnetincome" label="()" width="200" sortable>
			</el-table-column>
			<el-table-column prop="team" label="" sortable>
			</el-table-column>
			<el-table-column fixed="right" label="" width="120">
				<template slot-scope="scope">						
						<el-button type="primary"
							:disabled="scope.row.profitdistributed"
							size="small" 
							@click="profitAlloc(scope.$index, scope.row)"
						></el-button>	
						<!-- <el-button type="success"
							v-if="scope.row.profitdistributed"
							size="small" 
							@click="profitAlloc(scope.$index, scope.row)"
						></el-button>	 -->				
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar" style="text-align:left">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog 
				title="" 
				:visible.sync="profitSharePlanSubmitDialogVisible" 
				center
		>
			<el-col :span="24" style="text-align:left">
				<el-button type="primary" @click="handleAdd" icon="el-icon-search"></el-button>
			</el-col>
			<el-table
					:data="selusers" 
					stripe
					highlight-current-row 
					v-loading="listLoading"  
					style="width: 100%;text-align:left"
					height="300"
			>
				<el-table-column type="index" width="60">
				</el-table-column>
				<el-table-column prop="id" label="ID" width="150">
				</el-table-column>
				<el-table-column prop="name" label="" width="150">
				</el-table-column>
				<el-table-column prop="team" label="">
				</el-table-column>
				<el-table-column prop="propotion" label="(%)">
					<template slot-scope="scope">
  						<el-input-number 
  							v-model="scope.row.propotion" 
  							@change="handlePropotionChange(scope)" 
  							:min="1" 
  							:max="100"
  							:step="1" 
  							label=""
  							size="mini"
  							></el-input-number>
				    </template>
				</el-table-column>
			</el-table>
			<span slot="footer" class="dialog-footer">
				<el-alert
				    title="100%"
				    type="error"
				    :closable="false"
				    v-if="totalPropotion !== 100 && selusers.length !== 0"
				    show-icon
				    center
				    description="100%"
				>
				</el-alert>
				
				<el-alert
				    title=""
				    type="error"
				    :closable="false"
				    v-if="selusers.length === 0"
				    show-icon
				    center
				    description=""
				>
				</el-alert>

				<el-button 
					type="primary" 
					@click="profitSharePlanSubmit" 
					:disabled="this.selusers.length===0||this.totalPropotion !== 100"
					:loading="profitSharePlanLoading"
				></el-button>
			</span>
		</el-dialog>

		<!---->
		<el-dialog 
				title="" 
				:visible.sync="addFormVisible" 
				center
		>
			<el-col :span="24" class="toolbar" style="padding-bottom: 0px;text-align: left">
				<el-form :inline="true" :model="filters">
					<el-form-item>
						<el-input 
							v-model="filters.name" 
							placeholder="ID"
							size="mini"
						></el-input>
					</el-form-item>
					<el-form-item>
						<el-input 
							v-model="filters.name" 
							placeholder=""
							size="mini"
						></el-input>	
					</el-form-item>
					<el-form-item>
						<el-input 
							v-model="filters.name" 
							placeholder=""
							size="mini"
						></el-input>	
					</el-form-item>
					<el-form-item>
						<el-button-group>
							<el-button 
								type="primary" 
								@click="getUsers" 
								icon="el-icon-search"
								size="mini"
							></el-button>	
						</el-button-group>
					</el-form-item>
				</el-form>
			</el-col>

			<!---->
			<el-table 
				:data="users" 
				stripe
				highlight-current-row 
				v-loading="listLoading" 
				@selection-change="selUserchange" 
				style="width: 100%;text-align:left"
				height="300"
			>
				<el-table-column type="selection" width="55">
				</el-table-column>
				<el-table-column type="index" width="60">
				</el-table-column>
				<el-table-column prop="id" label="ID" width="150" sortable>
				</el-table-column>
				<el-table-column prop="name" label="" width="150"  sortable>
				</el-table-column>
				<el-table-column prop="team" label=""  sortable>
				</el-table-column>
			</el-table>

			<!---->
			<!-- <el-col :span="24" class="toolbar" style="text-align:left"> -->
			<span slot="footer" class="dialog-footer">
				<el-button type="primary" @click="batchSelectUser" :disabled="this.selusers.length===0"></el-button>
				<!-- <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
				</el-pagination> -->
			</span>
			<!-- </el-col> -->
		</el-dialog>

	</section>
</template>

<script>
	// import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import FileSaver from 'file-saver'
    import XLSX from 'xlsx'

	import { getUserListPage,getCustListPage, removeUser, batchRemoveUser, editCust, addUser, addProfitShareResult } from '@/api/api';

	export default {
		data() {
			return {
				filters: {
					name: ''
				},
				users: [],
				custs: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//
				selusers: [],
				currentProfitShareCust: {},  //
				profitSharePlanSubmitDialogVisible: false,//
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				editForm: {
					bankid: '',
					bankname: '',
					custid: '',
					custname: '',
					custtype: '',
					custnetincome: 0.0,
					team: ''
				},

				addFormVisible: false,//
				addLoading: false,
				addFormRules: {   //
					id: [
						{ required: true, message: 'ID', trigger: 'blur' }
					],
					name: [
						{ required: true, message: '', trigger: 'blur' }
					],
					role: [
						{ required: true, message: '', trigger: 'blur' }
					],
					team: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				addForm: {
					bankid: '',
					bankname: '',
					custid: '',
					custname: '',
					custtype: '',
					custnetincome: 0.0,
					team: ''
				},
				//
				Roles: [
							{
								id: 1,
							 	value:''
							},
							{
								id: 2,
								value:''
							},
							{
								id: 3,
								value: ''
							},
							{
								id: 4,
								value: ''
							}
						],
				//
				Teams: [
							{
								id: 1,
								value:''
							}, 
							{
								id: 2,
								value:''
							}, 
							{
								id: 3,
								value:''
							}, 
							{
								id: 4,
								value:''
							}, 
							{
								id: 5,
								value:''
							}, 
							{
								id: 6,
								value:''
							}, 
							{
								id: 7,
								value:''
							}
						],
				uploadFormVisible: false,
				totalPropotion: 0,
				profitSharePlan: [],  //
				profitSharePlanLoading: false  //

			}
		},
		methods: {
			//
			formatSex: function (row, column) {
				return row.sex == 1 ? '' : row.sex == 0 ? '' : '';
			},
			handleCurrentChange(val) {
				this.page = val;
				this.getCusts();
			},
			handlePropotionChange(value){
				console.log("value:",value)
				console.log("this.selusers:",this.selusers)
				//
				this.totalPropotion = 0
				for(var i = 0; i<this.selusers.length; i++){
					this.totalPropotion += this.selusers[i].propotion
				}
				console.log("this.totalPropotion:",this.totalPropotion)
			},
			//
			getCusts() {
				let para = {
					page: this.page,
					name: this.filters.name
				};
				this.listLoading = true;
				//NProgress.start();
				getCustListPage(para).then((res) => {
					this.total = res.data.total;
					this.custs = res.data.custs;
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//profitSharePlan
			profitSharePlanCreate(custmers){
				
				for(var i=0; i<custmers.length;i++){
					let subplan = {

					}
				}
			},
			//
			handleDel: function (index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { bankid: row.bankid, custid: row.custid };
					removeUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getCusts();
					});
				}).catch(() => {

				});
			},
			//
			profitAlloc: function (index, row) {
				if(row.allocated){
					this.selusers = []

					this.profitSharePlanSubmitDialogVisible = true;
					//
					this.totalPropotion = 0
					for(var i = 0; i<this.selusers.length; i++){
						this.totalPropotion += this.selusers[i].propotion
					}
					//
					this.currentProfitShareCust = row 
					console.log("this.currentProfitShareCust:",this.currentProfitShareCust)
				}else{
					this.$message({
			          message: '',
			          type: 'error'
			        });
				}
			},
			//
			handleAdd: function () {
				this.selusers = []
				this.addFormVisible = true;
				this.addForm = {
					bankid: '',
					bankname: '',
					custid: '',
					custname: '',
					custtype: '',
					custnetincome: 0.0,
					team: ''
				};
			},
			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							// para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							console.log("para:",para);
							editCust(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.profitSharePlanSubmitDialogVisible = false;
								this.getCusts();
							});
						});
					}
				});
			},
			//
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							addUser(para).then((res) => {
								this.addLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getCusts();
							});
						});
					}
				});
			},
			selUserchange(sels){
				//selusers

				this.selusers = sels
				
				console.log("this.selusers:",this.selusers)
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//
			batchRemove: function () {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { ids: ids };
					batchRemoveUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getCusts();
					});
				}).catch(() => {

				});
			},
			batchSelectUser(){
				//
				this.$confirm('', '', {
					type: 'warning'
				}).then(() => {
						//propotion
						for(var i = 0; i<this.selusers.length; i++){
							this.selusers[i].propotion = 1;
						}

						this.$message({
							message: '',
							type: 'success'
						});
						this.addFormVisible = false;
				}).catch(() => {

				});
			},
			handleUpload(){
				this.uploadFormVisible = true;       

			},
			handleUploadData(obj){
				var files = obj.files
				var fileReader = new FileReader();
	            fileReader.onload = function(ev) {
	                try {
	                    var data = ev.target.result,
	                        workbook = XLSX.read(data, {
	                            type: 'binary'
	                        }), // excel
	                        persons = []; // 
	                } catch (e) {
	                    console.log('');
	                    return;
	                }

	                // 
	                var fromTo = '';
	                // 
	                for (var sheet in workbook.Sheets) {
	                    if (workbook.Sheets.hasOwnProperty(sheet)) {
	                        fromTo = workbook.Sheets[sheet]['!ref'];
	                        console.log(fromTo);
	                        persons = persons.concat(XLSX.utils.sheet_to_json(workbook.Sheets[sheet]));
	                        // break; // 
	                    }
	                }

	                console.log(persons);
	            };

	            // 
	            fileReader.readAsBinaryString(files[0]);
			},
			getUsers() {
				let para = {
					page: this.page,
					name: this.filters.name
				};
				this.listLoading = true;
				//NProgress.start();
				getUserListPage(para).then((res) => {
					this.users = res.data.users;
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//
			profitSharePlanSubmit(){		

				//
				this.$confirm('', '', {
					type: 'warning'
				}).then(() => {
						this.profitSharePlanLoading = true
						console.log("","this.selusers:",this.selusers)
						//
						this.currentProfitShareCust.profitdistributed = true
						//
						// this.custs.some(u => {
					 //        if (u.bankid === this.currentProfitShareCust.bankid && u.custid === this.currentProfitShareCust.custid) {
					 //          u.profitdistributed = true
					 //          return true;
					 //        }
					 //    });

					    editCust(this.currentProfitShareCust).then((res) => {
								console.log("")
								this.getCusts();
						});
						//
						//
                        let para = {
                        	cust: this.currentProfitShareCust,
                        	plan: this.selusers
                        }

      //                   let para = []

      //                   //
						// for(var i=0; i<this.selusers.length; i++){
						// 	para.push({
						// 		cust: this.currentProfitShareCust,
						// 		plan: this.selusers[i]
						// 	})
						// }

                        console.log("para:",para)
						addProfitShareResult(para).then((res) => {
							this.profitSharePlanLoading = false

							this.$message({
								message: '',
								type: 'success'
							});
							this.profitSharePlanSubmitDialogVisible = false;
						});

				}).catch(() => {

				});
			}
		},
		mounted() {
			this.getCusts();
			this.getUsers();
		}
	}

</script>

<style scoped>

</style>


